import * as _             from 'lodash';
import {Component, Input} from '@angular/core';
import {Notifier}         from '../../services/notifier.service';


export class SimpleNotifierComponent {
  visible;
  message;
  timeout;
  classes;
  activeTime = 2000;
   address;
  
  constructor(private notifier: Notifier) {}

  ngOnInit() {
    this.notifier.registerHandle(this.address).subscribe(data => {
      _.extend(this, data, {visible: true});
      this.timeout = setTimeout(() => this.close(), this.activeTime);
    });    
  }

  close() {
    this.visible = false;
  }

  ngOnDestroy() {
    clearTimeout(this.timeout);
    this.notifier.deregisterHandle(this.address);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'ng-simple-notifier',
  template: `
    <div *ngIf = "visible" class = "ng-simple-notifier-container" 
    [ngClass] = "classes?.container">
      <div class = "ng-simple-notifier" [ngClass] = "classes?.notifier">
        <div class = "ng-simple-notifier-inner">
          <div class = "ng-simple-notifier-icon" [ngClass] = "classes?.icon"></div>
          <div class = "ng-simple-notifier-messages">
            <div class = "ng-simple-notifier-message" *ngFor = "let part of message">
              {{part}}
            </div>
          </div>
          <div class = "ng-simple-notifier-closer" [ngClass] = "classes?.closer"
          (click) = "close()">
            <span *ngIf = "!classes?.closer">&times;</span>
          </div>
        </div>
      </div>
    </div>
  `
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: Notifier, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'address': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
